.button span {
  border-bottom: solid 1px $color-white;
}

.hero {
  padding-bottom: 0;
  @include laptop {
    padding-bottom: 123px;
  }
}
.hero__buttons {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include laptop {
    align-items: flex-start;
  }
}

.hero__buttons .button-primary {
  margin-bottom: 30px;
  max-width: 325px;
  padding: 16px 28px;
  @include laptop {
    margin-bottom: 51px;
    max-width: 398px;
    padding: 14px 40px;
  }
}

.hero__buttons .button-secondary {
  max-width: 251px;
  padding: 16px 31px;
  @include laptop {
    margin-bottom: 65px;
    max-width: 308px;
    padding: 14px 40px;
  }
}

.hero__buttons p {
  display: none;

  @include laptop {
    display: block;
    max-width: 468px;
    color: $color-gray;
    font-size: 13px;
    font-weight: 600;
    letter-spacing: 0.4;
    line-height: 24px;
    margin-left: 8px;
  }
}

.hero__image p {
  display: block;
  max-width: 468px;
  color: $color-gray;
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.4;
  line-height: 24px;
  margin-left: 8px;
  margin-bottom: 34px;
  @include tablet {
    font-size: 18px;
  }
  @include laptop {
    font-size: 13px;
    display: none;
  }
}
.hero__greeting {
  color: $color-primary;
  letter-spacing: 4.2px;
  font-size: 14px;
  font-weight: 900;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 11px;
  @include laptop {
    text-align: left;
    margin-bottom: 32px;
  }
}

.hero__image {
  width: 100%;

  @include laptop {
    max-width: 57%;
  }
}

.hero__inner {
  display: flex;
  flex-direction: column;
  @include laptop {
    margin-bottom: 15px;
    flex-direction: row;
  }
}

.hero__image img {
  display: block;
  margin-top: -35px;
  margin-right: auto;
  margin-bottom: 44px;
  margin-left: auto;
  height: 400px;

  @include laptop {
    height: auto;
  }

  @include desktop {
    margin-top: -110px;
    margin-bottom: 0;
  }
}

.hero__logos {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  @include laptop {
    flex-direction: row;
    max-width: 940px;
  }
}

.hero__text {
  position: relative;
  width: 100%;
  text-align: center;
  padding-top: 35px;

  @include laptop {
    max-width: 43%;
    padding-top: 98px;
    text-align: left;
  }
}
.hero__logos img {
  height: 100%;
}

.hero__logos img:first-child {
  width: 98px;
  margin-bottom: 48px;

  @include laptop {
    margin-bottom: 0px;
    width: 135px;
  }
}
.hero__logos img:nth-child(2) {
  width: 73px;

  @include laptop {
    width: 109px;
  }
}

.hero__logos img:nth-child(3) {
  width: 51px;

  @include laptop {
    width: 75px;
  }
}

.hero__logos img:nth-child(4) {
  width: 76px;

  @include laptop {
    margin-left: 0;
    width: 106px;
  }
}

.hero__logos img:nth-child(5) {
  width: 194px;

  @include laptop {
    width: 210px;
  }
}

.hero__title {
  font-size: 32px;
  line-height: 46px;
  letter-spacing: -1.3px;
  margin-bottom: 40px;
  padding: 0 35px;
  font-weight: 600;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 35px;
  max-width: 320px;

  @include laptop {
    max-width: 100%;
    font-size: 38px;
    line-height: 55px;
    max-width: 340px;
    margin-left: 0;
    margin-bottom: 46px;
    padding: 0;
  }
}

.hero__title a {
  color: $color-secondary;
}
