.info-box {
  display: flex;
  flex-direction: column;

  @include laptop {
    flex-direction: row;
  }
}

.info-box p {
  font-size: 16px;
  line-height: 32px;
  letter-spacing: 0;
  font-weight: 600;
  @include laptop {
    font-size: 18px;
    line-height: 34px;
    margin-bottom: 42px;
    max-width: 845px;
  }
}

.info-box__content {
  @include laptop {
    padding-top: 21px;
  }
}
.info-box-wrap {
  display: flex;
  flex-direction: column-reverse;

  @include laptop {
    flex-direction: column;
  }
}

.image-wrap-red {
  width: 100%;
  min-height: 183px;
  background-color: $color-primary;
  padding: 24px 91px 0px 105px;
  margin-bottom: 19px;

  @include laptop {
    margin-right: 138px;
    width: 406px;
    height: 398px;
    margin-bottom: 0;
    padding: 36px 30px 48px 40px;
  }
}

.image-wrap-teal {
  background-color: $color-secondary;
  width: 100%;
  min-height: 183px;
  margin-bottom: 40px;
  @include laptop {
    width: 406px;
    height: 398px;
    margin-left: 110px;
    margin-bottom: 0px;
  }
}

.image-wrap-teal img {
  margin-left: 20%;
  margin-top: 6%;
  @include tablet {
    margin-left: 40%;
    margin-top: 2%;
  }
  @include laptop {
    margin-left: -20px;
    width: 480px;
    max-width: 110%;
    height: 365px;
    margin-top: 0;
  }
}

.info-box--reverse .button-secondary {
}
.image-wrap-beige {
  background-color: $color-beige;
  width: 100%;
  min-height: 183px;
  @include laptop {
    width: 406px;
    height: 398px;
    padding: 65px 0px 65px 68px;
    margin-right: 127px;
  }
}

.info-box--reverse .buttons-wrap {
  margin-bottom: 80px;

  @include laptop {
    margin-bottom: 0px;
  }
}
.info-box:last-child .info-box__content {
  @include laptop {
    padding-top: 51px;
  }
}

.image-wrap-red img {
  height: 142px;
  margin-bottom: 37px;
  @include tablet {
    margin-left: 37%;
  }
  @include laptop {
    height: 326px;
    margin-left: 0;
    margin-bottom: 0px;
  }
}

.image-wrap-beige img {
  @include laptop {
    margin-left: 4px;
    max-width: 110%;
  }
}
.info-box--reverse .button-ghost {
  display: none;

  @include laptop {
    display: block;
    border: solid $color-secondary 2px;
    color: $color-secondary;
    &:focus,
    &:hover,
    &:active {
      background: #33a6a726 0% 0%;
      color: $color-secondary;
    }
  }
}
.info__title {
  font-size: 26px;
  margin-bottom: 24px;
  max-width: 82%;
  @include laptop {
    font-size: 30px;
    margin-bottom: 41px;
    max-width: 100%;
  }
}

.info-box--reverse {
  display: flex;
  @include laptop {
    flex-direction: row-reverse;
  }
}

.info-box--reverse p {
  margin-bottom: 34px;

  @include laptop {
    margin-bottom: 50px;
  }
}

.info-box--reverse .info__title {
  margin-bottom: 20px;
  @include laptop {
    margin-bottom: 50px;
  }
}

.info-box--reverse .image-wrap {
  @include laptop {
    margin-left: 138px;
  }
}

.info-box--reverse .info__title {
  max-width: 71%;
  @include laptop {
    max-width: 100%;
  }
}

.info-box:not(last-child) {
  @include laptop {
    margin-bottom: 99px;
  }
}

.info-box:last-child {
  display: none;

  @include laptop {
    display: flex;
  }
}

.info-box--reverse .info-box__content {
  @include laptop {
    padding-top: 38px;
  }
}

.buttons-wrap .button-primary {
  padding: 14px 53px;

  @include laptop {
    margin-right: 33px;
  }
}

.buttons-wrap .button-secondary {
  padding: 14px 50px;
  @include laptop {
    margin-right: 33px;
  }
}

.info-box:first-child p {
  margin-bottom: 34px;
  @include laptop {
    margin-bottom: 50px;
  }
}

.info-box:first-child .button-ghost {
  display: none;

  @include laptop {
    display: block;
  }
}

.buttons-wrap {
  display: flex;
  flex-direction: row;
}
