.menu-button {
  position: relative;
  width: 40px;
  height: 40px;
  padding: 0;
  cursor: pointer;
  transition: all 0.5s;
  text-align: left;
  background: none;
  border: 0;

  div {
    background: #000;
    position: absolute;
    height: 4px;
    width: 40px;
    border-radius: 4px;
  }

  div:first-child {
    top: 0;
    transition: top 0.15s 0.15s, transform 0.15s;
  }

  div:nth-child(2),
  div:nth-child(3) {
    transition: top 0.15s 0.15s, transform 0.15s;
  }

  div:nth-child(2) {
    top: 14px;
    width: 24px;
  }

  div:nth-child(3) {
    top: 28px;
    width: 35px;
  }

  &.is-open {
    div:first-child {
      top: 10px;
      transform: rotate(45deg);
      transition: top 0.15s, transform 0.15s 0.15s;
    }

    div:nth-child(2),
    div:nth-child(3) {
      top: 10px;
      transform: rotate(-45deg);
      transition: top 0.15s, transform 0.15s 0.15s;
      width: 40px;
    }
  }
}
